import { v1 } from "uuid";

const initialState = {
  dialogs: [
    { id: v1(), name: "" },
    { id: v1(), name: "" },
    { id: v1(), name: "" },
    { id: v1(), name: "" },
    { id: v1(), name: "" },
    { id: v1(), name: "" },
    { id: v1(), name: "" },
  ],
  messages: [
    { id: v1(), message: "" },
    { id: v1(), message: " ?" },
  ],
};

export const dialogsReducer = (
  state: DialogsStateType = initialState,
  action: DialogsActionsType,
): DialogsStateType => {
  switch (action.type) {
    case "ADD-MESSAGE": {
      const newMessage = { id: v1(), message: action.newMessageBody };
      return {
        ...state,
        messages: [...state.messages, newMessage],
      };
    }
    default:
      return state;
  }
};

// actions

export const addMessage = (newMessageBody: string) => {
  return {
    type: "ADD-MESSAGE",
    newMessageBody,
  } as const;
};

export type MessageType = {
  id: string;
  message: string;
};
export type DialogType = {
  id: string;
  name: string;
};
export type DialogsStateType = {
  messages: Array<MessageType>;
  dialogs: Array<DialogType>;
};
export type DialogsActionsType = ReturnType<typeof addMessage>;
